@use "global" as *;

// 
$breakpointInner: $innerWidth + strip-unit($padding-pc) * 2;

// 
html {
    // 376px ~ 767px
    font-size: 100%;

  // ~ 375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

  // 768px ~ 
  @include mq(md) {
    font-size: vw(strip-unit($breakpointInner), 16);
  }

  //  ~
  @media ( min-width: $breakpointInner ) {
    font-size: 100%;
  }
}


// 
body {
  font-family: $font-main;
  color: $black;
}

// 
a,
button {
  @include mq(md) {
    transition: .3s;
    &:hover {
      opacity: 0.7;
      cursor: pointer;
    }
  }
}